import React from 'react'
import { motion } from 'framer-motion';
import { useJobApplicationsListContext } from "./JobApplicationsListContext";
import './DashboardMainAreaNoListings.css'


function DashboardMainAreaNoListings() {

    const list = useJobApplicationsListContext();

  return (
    list.length === 0 ? (
        <div className="DashboardSuggestionWrapperWhenEmpty">
          <motion.span
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{
              duration: 1.5,
              delay: 1,
              type: "spring",
              bounce: 0.5,
            }}
            className="DashboardSuggestion"
          >
            You do not have added any Job Applications to your listings yet!
          </motion.span>
          <motion.span
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{
              duration: 1.5,
              delay: 1.5,
              type: "spring",
              bounce: 0.5,
            }}
            className="DashboardSuggestionCallToAction"
          >
            {" "}
            <h3>👈</h3> Just add you first listing using the menu on the left!
          </motion.span>
        </div>
      ) : (
        list.length < 5 && (
          <span className="DashboardSuggestion">
            Click on the application to open its link!
          </span>
        )
      )
  )
}

export default DashboardMainAreaNoListings