import React, { useState } from "react";
import { createPortal } from "react-dom";
import "./SignUp.css";
import { motion, AnimatePresence } from "framer-motion";

export default function SignUp() {
  const [modalIsVisible, SetModalIsVisible] = useState(false);

  return (
    <>
      <button
        onClick={() => SetModalIsVisible(true)}
        className="HeaderSignUpButton"
      >
        Sign up
      </button>

      {modalIsVisible &&
        createPortal(
          <AnimatePresence mode="wait">
            <motion.div
              className="SignUpWrapper"
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
            >
              <AnimatePresence mode="wait">
                <motion.div
                  className="SignUpContainer"
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  exit={{ opacity: 0 }}
                >
                  <div className="SignUpTopBar">
                    <h1>Sign up</h1>
                    <button
                      onClick={() => SetModalIsVisible(false)}
                      className="SignUpCloseButton"
                    >
                      x
                    </button>
                  </div>
                  <div className="SignUpList">
                    <div className="SignUpLogoWrapper">
                      <img
                        alt="SignUp-JAT-Job-Applications-Tracker-logo-img"
                        className="SignUpLogo"
                        src={require("../assets/JAT_logo.svg").default}
                      />
                    </div>
                    <form className="SignUpForm">
                      <div className="SignUpFieldSet">
                        <label>Email:</label>
                        <input type="text" />
                      </div>
                      <div className="SignUpFieldSet">
                        <label>Password:</label>
                        <input type="password" />
                      </div>
                      <button className="SignUpSubmitButton" type="submit">
                        Sign up
                      </button>
                    </form>
                  </div>
                </motion.div>
              </AnimatePresence>
            </motion.div>
          </AnimatePresence>,
          document.getElementById("portal")
        )}
    </>
  );
}
